	module.exports = {"pathContext":{"docHtml":"<h1>strip-bom <a href=\"https://travis-ci.org/sindresorhus/strip-bom\"><img src=\"https://travis-ci.org/sindresorhus/strip-bom.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Strip UTF-8 <a href=\"http://en.wikipedia.org/wiki/Byte_order_mark#UTF-8\">byte order mark</a> (BOM) from a string</p>\n</blockquote>\n<p>From Wikipedia:</p>\n<blockquote>\n<p>The Unicode Standard permits the BOM in UTF-8, but does not require nor recommend its use. Byte order has no meaning in UTF-8.</p>\n</blockquote>\n<h2>Install</h2>\n<pre><code>$ npm install --save strip-bom\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const stripBom = require('strip-bom');\n\nstripBom('\\uFEFFunicorn');\n//=> 'unicorn'\n</code></pre>\n<h2>Related</h2>\n<ul>\n<li><a href=\"https://github.com/sindresorhus/strip-bom-cli\">strip-bom-cli</a> - CLI for this module</li>\n<li><a href=\"https://github.com/sindresorhus/strip-bom-buf\">strip-bom-buf</a> - Buffer version of this module</li>\n<li><a href=\"https://github.com/sindresorhus/strip-bom-stream\">strip-bom-stream</a> - Stream version of this module</li>\n</ul>\n<h2>License</h2>\n<p>MIT © <a href=\"https://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/strip-bom/readme","proj":"argo-ci"}}

